import * as React from 'react';
import Layout from '../layouts/main';
import { NextPage } from 'next';
import DemoForm from '../components/DemoForm';

import { FormGroup, Button, Tooltip, Intent, Toaster, Icon } from '@blueprintjs/core';
import { IconNames } from '@blueprintjs/icons';
import { delay } from 'bluebird';
import { isRequired } from '../src/core';

import { BlueprintFormMiddlewareProps } from '../src/share';
const MyButtonsMw = (props: BlueprintFormMiddlewareProps) => {
  const {
    parent,
    next,
    onChange,
    formProps: { defaultData },
  } = props;

  const toaster = React.useRef<any>(null);
  const [submitting, setSubmitting] = React.useState(false);

  // if not on the root node, pass the control to the next middleware
  if (parent) return next(props);

  const handleSubmit = async function() {
    setSubmitting(true);
    await delay(1000);
    // pactically, should check unmounted here
    setSubmitting(false);
    toaster.current && toaster.current.show({ message: 'Success!', intent: Intent.SUCCESS });
  };
  const handleClear = () => onChange({});
  const handleReset = () => onChange(defaultData);

  return (
    <>
      {next(props) /* render the body of the form */}
      <Button intent={Intent.PRIMARY} onClick={handleSubmit} loading={submitting}>
        Submit
      </Button>{' '}
      <Button onClick={handleClear}>Clear</Button> <Button onClick={handleReset}>Reset</Button>
      <Toaster ref={toaster} />
    </>
  );
};

const MyFormItemTemplate = (props: BlueprintFormMiddlewareProps) => {
  const { schema, dataPath, parent, next } = props;

  if (
    typeof schema === 'boolean' ||
    schema.type === 'object' ||
    schema.type === 'array' ||
    // skip if current node is under an array
    (parent && typeof parent.schema !== 'boolean' && parent.schema.type === 'array')
  )
    return next(props);

  const info = schema.description ? (
    <Tooltip content={schema.description}>
      <Icon icon={IconNames.INFO_SIGN} />
    </Tooltip>
  ) : null;

  return (
    <FormGroup
      label={
        <>
          {schema.title || dataPath[dataPath.length - 1]} {info}
        </>
      }
      helperText={schema.description}
      labelInfo={<span>{isRequired(props) ? '(required)' : null}</span>}
    >
      {next(props)}
    </FormGroup>
  );
};

const code = `const schema = {
    type: 'object',
    properties: {
      username: { type: 'string', description: 'Username description' },
      firstname: { type: 'string' },
      lastname: { type: 'string' },
      email: { type: 'string', description: 'Email description' },
    },
  };

  const defaultData = {
  username: 'jhon',
  firstname: 'Jhon',
  lastname: 'Smith',
};

const middlewares = [
  MyButtonsMw,
  FieldsetTemplateMw,
  MyFormItemTemplate,
  ...schemaMws,
  InputMw,
  NotSupportedMw,
];

render(
  <Form
    defaultData={defaultData}
    layout="vertical"
    schema={schema}
    middlewares={middlewares}
  />
);
`;

const IndexPage: NextPage = () => {
  return (
    <Layout>
      <DemoForm code={code} scope={{ MyButtonsMw, MyFormItemTemplate }} />
    </Layout>
  );
};

export default IndexPage;
